		startCountdown() {
			this.countingDown = true;
			let count = 10;

			const intervalId = setInterval(() => {
				this.countdown = count;
				count--;

				if (count < 0) {
					clearInterval(intervalId);
					this.countingDown = false;
					this.countdown = 10; //  
